import { VariableDataType } from '@/component/variable_manager/types';

/**
 * 
 */
export class VariableManagerUtil {
  /**
   * 
   * @param value 
   * @returns 
   */
  public static inferDataType(value: any): VariableDataType {
    if (Array.isArray(value)) {
      return 'array';
    } else if (typeof value === 'boolean') {
      return 'boolean';
    } else if (typeof value === 'number') {
      return 'number';
    } else if (typeof value === 'object' && value !== null) {
      return 'object';
    }
    return 'string';
  }

  /**
   * 
   * @param dataType 
   * @returns 
   */
  public static getDefaultValueForType(dataType: VariableDataType): any {
    switch (dataType) {
      case 'string':
        return '';
      case 'number':
        return 0;
      case 'boolean':
        return false;
      case 'array':
        return [];
      case 'object':
        return {};
      default:
        return '';
    }
  }

  /**
   * 
   * @param existingKeys 
   * @param baseName  'newKey'
   * @returns 
   */
  public static generateUniqueKey(existingKeys: Set<string>, baseName: string = 'newKey'): string {
    let newKey = baseName;
    let counter = 1;
    while (existingKeys.has(newKey)) {
      newKey = `${baseName}${counter}`;
      counter++;
    }
    return newKey;
  }
}
