import { eventSource } from '@sillytavern/script';
import { SlashCommand } from '@sillytavern/scripts/slash-commands/SlashCommand';
import { ARGUMENT_TYPE, SlashCommandNamedArgument } from '@sillytavern/scripts/slash-commands/SlashCommandArgument';
import { SlashCommandParser } from '@sillytavern/scripts/slash-commands/SlashCommandParser';

import log from 'loglevel';

export async function slashEventEmit(named_args: any): Promise<any> {
  const event: string = named_args.event;
  const data: string[] = named_args.data ?? [];

  eventSource.emit(event, ...data);

  log.info(`[Event][/event-emit]  '${event}' , : ${JSON.stringify(data)}`);

  return event;
}

export function initSlashEventEmit() {
  SlashCommandParser.addCommandObject(
    SlashCommand.fromProps({
      name: 'event-emit',
      callback: slashEventEmit,
      returns: '',
      namedArgumentList: [
        SlashCommandNamedArgument.fromProps({
          name: 'event',
          description: '',
          typeList: [ARGUMENT_TYPE.STRING],
          isRequired: true,
        }),
        SlashCommandNamedArgument.fromProps({
          name: 'data',
          description: '',
          typeList: [ARGUMENT_TYPE.STRING],
          isRequired: false,
          acceptsMultiple: true,
        }),
      ],
      unnamedArgumentList: [],
      helpString: `
    <div>
         \`event\` , .
         listener , .
         STScript ,  string ;  number , .
    </div>
    <div>
        <strong>Example:</strong>
        <ul>
            <li>
                <pre><code class="language-stscript">/event-emit event=""</code></pre>
            </li>
            <li>
                <pre><code class="language-stscript">/event-emit event="" data={{getvar::}} data=8 data= {{user}}</code></pre>
            </li>
            <li>
                <pre><code class="language-stscript">/event-emit event="" data=" " data={{user}}</code></pre>
            </li>
        </ul>
    </div>
  `,
    }),
  );
}
